import cn from "classnames";
import { NavLink } from "react-router-dom";
import styles from "./navigation.module.css";

export default function Navigation() {
  const pagesNames = [
    { name: "", route: "/" },
    { name: " ", route: "/about-school" },
    { name: " ", route: "/about-trainer" },
    { name: " ", route: "/first-lesson" },
    { name: "", route: "/goods" },
  ];

  const getStyles = (route) => {
    if (route === "/goods") return cn(styles.link, styles.goods);
    return styles.link;
  };
  return (
    <section className={styles.container}>
      {pagesNames.map(({ name, route }, index) => (
        <NavLink
          to={`${route}`}
          key={index}
          className={({ isActive }) =>
            !isActive ? getStyles(route) : cn(styles.link, styles.active)
          }
          activeClassName={styles.active}
        >
          {name}
        </NavLink>
      ))}
    </section>
  );
}
